body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    margin: 1rem;
    position: relative;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 60%;
    overflow: hidden;
    text-align: center;
    background:#6CADDC;

  }
  .card > .img-container > img {
    width: 60%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  
  img {
    min-width: 200px;
    min-height: 200px
  }
  body {
    background-color: black
  }

  .card-img-top:hover {
    /* opacity: 0.8; */
    border: 5px solid white;
  }
.jumbotronJumbo {
    color: white;
    background: url('https://wallpapercave.com/wp/RsyHRBx.jpg');
    padding: 0px 0px;
}

.jumboOpaque {
   background-color: rgba(255,255,255, 0.3);
   min-height: 435px;
   padding-top: 80px;
}

.scoreText {
    font-size: 36px;
    margin-top: 50px;
}
